import React from "react";
import { useState, useEffect } from "react";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import IntentRegNluToolbar from "./IntentRegNluToolbar";
import { getIntents, getTrainingData } from "./actions/nlu.actions";
import IntentRegNluList from "./IntentRegNluList";

const containerStyle = makeStyles(theme => ({
  root: {
    height: "100%"
  }
}));
const getRegNluData = async (project_id, params) => {
  const response = await getTrainingData(project_id, params);
  console.log(response);
  return response;
};

function IntentRegNluContainer({ project_id, name, index }) {
  const [data, setData] = useState([]);
  const [total, setTotal] = useState(0);
  const [intents, setIntents] = useState([]);
  const [loading, setLoading] = useState(false);
  const [limit, setLimit] = useState(50);
  const [offset, setOffset] = useState(0);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const response = await getTrainingData(project_id, {
          q: "",
          intent: "",
          limit: limit,
          offset: offset
        });
        console.log(response);
        setData(response.data);
        setTotal(response.headers["x-total-count"]);
      } catch (e) {
        console.log(e);
      }

      setLoading(false);
    };
    fetchData();
    const fetchIntentData = async () => {
      setLoading(true);
      try {
        const response = await getIntents(project_id);
        console.log(response);
        setIntents(response.data);
      } catch (e) {
        console.log(e);
      }

      setLoading(false);
    };

    fetchIntentData();
  }, [project_id]);

  const onRetrieve = async (project_id, params) => {
    console.log(project_id, params);

    try {
      const response = await getTrainingData(project_id, params);
      console.log(response);
      setData(response.data);
      setTotal(response.headersheaders["x-total-count"]);
    } catch (e) {
      console.log(e);
    }

    try {
      const response = await getIntents(project_id);
      console.log(response);
      setIntents(response.data);
    } catch (e) {
      console.log(e);
    }

    setLoading(false);
  };
  if (loading) {
    console.log("loading....");
    return null;
  } else if (data.length === 0) {
    console.log("data empty");
  } else {
    console.log("data load", data);
    console.log("data total", total);
  }

  return (
    <>
      {/* <IntentConvMngTable
        data={data}
        name={name}
        index={index}
        onRetrieve={onRetrieve}
      /> */}
      <IntentRegNluToolbar onRetrieve={onRetrieve} />
      <Container style={{ height: "100%" }}>
        <IntentRegNluList
          data={data}
          intents={intents}
          name={name}
          index={index}
          total={total}
          limit={limit}
          offset={offset}
          onRetrieve={onRetrieve}
        />
      </Container>
    </>
  );
}

export default IntentRegNluContainer;
