import axios from "axios";

export const GET_SUGGESTIONS_URL = "/api/projects";
export const GET_INTENTS_URL = "/api/projects/";

export function getSuggetions(project_id, { q, intent, limit, offset }) {
  return axios.get(
    GET_SUGGESTIONS_URL +
      "/" +
      project_id +
      "/" +
      "logs" +
      "?" +
      "q=" +
      q +
      "&intent=" +
      intent +
      "&limit=" +
      limit +
      "&offset=" +
      offset
  );
}

export function getIntents(project_id, params) {
  return axios.get(GET_INTENTS_URL + "/" + project_id + "/" + "intents");
}

export function getTrainingData(project_id, { q, intent, limit, offset }) {
  return axios.get(
    GET_SUGGESTIONS_URL +
      "/" +
      project_id +
      "/" +
      "data" +
      "?" +
      "q=" +
      q +
      "&intent=" +
      intent +
      "&limit=" +
      limit +
      "&offset=" +
      offset
  );
}
