import React from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
// import ListItem from "@material-ui/core/ListItem";
// import ListItemText from "@material-ui/core/ListItemText";
// import List from "@material-ui/core/List";
import PropTypes from "prop-types";
import IntentConvMngItem from "./IntentConvMngItem";
import { FixedSizeList } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";
import InfiniteLoader from "react-window-infinite-loader";
import { getSuggetions } from "./actions/nlu.actions";

let items = {};
let requestCache = {};

const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    height: "100%",
    maxWidth: 1500,
    backgroundColor: theme.palette.background.paper
  }
}));

const renderRow = props => {
  const { index, style, data, total, limit, offset } = props;
  const item = items[index];

  console.log("renderRow", index);

  return (
    <IntentConvMngItem
      index={index}
      style={style}
      data={item}
      total={total}
      limit={limit}
      offset={offset}
    />
  );
};
renderRow.propTypes = {
  index: PropTypes.number.isRequired,
  style: PropTypes.object.isRequired
};

const isItemLoaded = ({ index }) => !!items[index];

const loadMoreItems = (visibleStartIndex, visibleStopIndex) => {
  console.log(visibleStartIndex, visibleStopIndex);
  const key = [visibleStartIndex, visibleStopIndex].join(":"); // 0:10
  if (requestCache[key]) {
    return;
  }

  const length = visibleStopIndex - visibleStartIndex;
  const visibleRange = [...Array(length).keys()].map(
    x => x + visibleStartIndex
  );
  const itemsRetrieved = visibleRange.every(index => !!items[index]);

  if (itemsRetrieved) {
    requestCache[key] = key;
    return;
  }

  return getSuggetions("default", {
    q: "",
    intent: "",
    limit: length,
    offset: visibleStartIndex
  })
    .then(response => response.data)
    .then(data => {
      // data.records.forEach((city, index) => {
      //   items[index + visibleStartIndex] = city.fields;
      // });
      data.forEach((item, index) => {
        items[index + visibleStartIndex] = item;
      });
      console.log(data);
    })
    .catch(error => console.error("Error:", error));
};

