const IntentConvMngList = ({
  data,
  name,
  index,
  total,
  limit,
  offset,
  onRetrieve
}) => {
  const classes = useStyles();
  console.log(data);
  return (
    <AutoSizer>
      {({ height, width }) => (
        <InfiniteLoader
          isItemLoaded={isItemLoaded}
          loadMoreItems={loadMoreItems}
          itemCount={total}
        >
          {({ onItemsRendered, ref }) => (
            // <AutoSizer>
            //   {({ height, width }) => (
            <FixedSizeList
              className="List"
              height={height}
              itemCount={total}
              itemSize={80}
              width={width}
              // itemData={data}
              ref={ref}
              onItemsRendered={onItemsRendered}
            >
              {renderRow}
            </FixedSizeList>
            //   )}
            // </AutoSizer>
          )}
        </InfiniteLoader>
      )}
    </AutoSizer>
  );
};

export default IntentConvMngList;

