import React from "react";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Select from "react-select";
import {
  Checkbox,
  Typography,
  Grid,
  ListItemIcon,
  ListItemSecondaryAction,
  Box,
  Tooltip,
  Divider
} from "@material-ui/core";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import DeleteIcon from "@material-ui/icons/Delete";
import IconButton from "@material-ui/core/IconButton";
import objectPath from "object-path";
import DoubleArrowIcon from "@material-ui/icons/DoubleArrow";

const StyledListItemText = withStyles({
  root: {
    width: "20%",
    maxWidth: "600px"
  },
  multiline: {
    marginRight: "10px"
  }
})(ListItemText);

const StyledDivider = withStyles({
  root: {
    height: "25px",
    width: "1.5px"
  }
})(Divider);
const useStyles = makeStyles(theme => ({
  root: {
    width: "30%"
  }
}));

const customStyles = {
  container: (provied, state) => ({
    ...provied,
    width: "40%",
    maxWidth: "500px",
    color: state.isSelected ? "red" : "blue"
  })
};

const IntentConvMngItem = ({ index, data, style }) => {
  const classes = useStyles();
  //console.log(objectPath.get(data, "user_input"));
  //   console.log(
  //     objectPath
  //       .get(objectPath.get(data, "user_input"), "intent")
  //       .confidence.toFixed(3)
  //   );
  //   console.log(
  //     objectPath.get(objectPath.get(data, "user_input"), "intent_ranking")
  //   );
  if (!data) return null;
  let intent_ranking = [];
  // console.log("IntentConvMngItem data", data);
  objectPath
    .get(objectPath.get(data, "user_input"), "intent_ranking")
    .map(value => {
      //   console.log(value);
      const item = {
        value: value.name,
        label: value.name + " (" + value.confidence.toFixed(3) + ")"
      };
      intent_ranking.push(item);
    });

  //   console.log(intent_ranking);
  // Object.defineProperty(
  //   objectPath
  //   .get(objectPath.get(data, "intent_ranking"),
  // )
  const handleChange = selectedOption => {
    console.log(`Option selected:`, selectedOption);
  };
  return (
    <>
      <ListItem key={data.id} style={style}>
        <Grid
          container
          direction="row"
          justify="flex-start"
          alignItems="center"
        >
          <ListItemIcon>
            <Checkbox
              edge="start"
              // checked={checked.indexOf(value) !== -1}
              tabIndex={-1}
              disableRipple
              // inputProps={{ "aria-labelledby": labelId }}
            />
          </ListItemIcon>
          {/* <ListItemText className={classes.root} primary={`Item ${index + 1}`} /> */}
          <StyledListItemText
            primary={objectPath.get(data, "user_input").text}
            secondary={
              objectPath.get(objectPath.get(data, "user_input"), "intent")
                .name +
              " (" +
              objectPath
                .get(objectPath.get(data, "user_input"), "intent")
                .confidence.toFixed(3) +
              ")"
            }
          />
          {/* <StyledDivider orientation="vertical" /> */}
          <Select
            styles={customStyles}
            options={intent_ranking}
            placeholder="의도 선택..."
            isSearchable={false}
            onChange={handleChange}
            defaultValue={intent_ranking.filter(
              option =>
                option.value ===
                objectPath.get(objectPath.get(data, "user_input"), "intent")
                  .name
            )}
          ></Select>
          {/* <StyledDivider orientation="vertical" /> */}
          <Box>
            <Tooltip title="저장">
              <IconButton edge="end" aria-label="Save">
                <DoubleArrowIcon />
              </IconButton>
            </Tooltip>
          </Box>
          {/* <StyledDivider orientation="vertical" /> */}
          <Box>
            <Tooltip title="삭제">
              <IconButton edge="end" aria-label="Delete">
                <DeleteIcon />
              </IconButton>
            </Tooltip>
          </Box>
        </Grid>
      </ListItem>
      <Divider />
    </>
  );
};

export default IntentConvMngItem;
