import React from "react";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Select from "react-select";
import {
  Checkbox,
  Typography,
  Grid,
  ListItemIcon,
  ListItemSecondaryAction,
  Box,
  Tooltip,
  Divider
} from "@material-ui/core";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import DeleteIcon from "@material-ui/icons/Delete";
import IconButton from "@material-ui/core/IconButton";
import objectPath from "object-path";
import DoubleArrowIcon from "@material-ui/icons/DoubleArrow";

const StyledListItemText = withStyles({
  root: {
    width: "20%",
    maxWidth: "600px"
  },
  multiline: {
    marginRight: "10px"
  }
})(ListItemText);

const StyledDivider = withStyles({
  root: {
    height: "25px",
    width: "1.5px"
  }
})(Divider);
const useStyles = makeStyles(theme => ({
  root: {
    width: "30%"
  }
}));

const customStyles = {
  container: provied => ({
    ...provied,
    width: "40%",
    maxWidth: "500px"
  })
};

const IntentRegNluItem = ({ index, data, intents }) => {
  const classes = useStyles();
  console.log("IntentRegNluItem data", data);
  console.log("IntentRegNluItem intents", intents);
  //console.log(objectPath.get(data, "user_input"));
  //   console.log(
  //     objectPath
  //       .get(objectPath.get(data, "user_input"), "intent")
  //       .confidence.toFixed(3)
  //   );
  //   console.log(
  //     objectPath.get(objectPath.get(data, "user_input"), "intent_ranking")
  //   );
  if (!data) return null;
  if (!intents) return null;
  let intentList = [];

  intents.map(value => {
    //   console.log(value);
    const item = {
      value: value.intent,
      label: value.intent
    };
    intentList.push(item);
  });

  //   console.log(intent_ranking);
  // Object.defineProperty(
  //   objectPath
  //   .get(objectPath.get(data, "intent_ranking"),
  // )

  return (
    <>
      <ListItem key={data.id}>
        <Grid
          container
          direction="row"
          justify="flex-start"
          alignItems="center"
        >
          <ListItemIcon>
            <Checkbox
              edge="start"
              // checked={checked.indexOf(value) !== -1}
              tabIndex={-1}
              disableRipple
              // inputProps={{ "aria-labelledby": labelId }}
            />
          </ListItemIcon>
          {/* <ListItemText className={classes.root} primary={`Item ${index + 1}`} /> */}
          <StyledListItemText
            primary={objectPath.get(data, "text")}
            secondary={
              objectPath.get(data, "intent") +
              " (" +
              objectPath.get(data, "id") +
              ")"
            }
          />
          {/* <StyledDivider orientation="vertical" /> */}
          <Select
            styles={customStyles}
            options={intentList}
            placeholder="의도(intent) 선택..."
            value={intentList.filter(
              option => option.label === objectPath.get(data, "intent")
            )}
          ></Select>
          {/* <StyledDivider orientation="vertical" /> */}
          <Box>
            <Tooltip title="저장">
              <IconButton edge="end" aria-label="Save">
                <DoubleArrowIcon />
              </IconButton>
            </Tooltip>
          </Box>
          {/* <StyledDivider orientation="vertical" /> */}
          <Box>
            <Tooltip title="삭제">
              <IconButton edge="end" aria-label="Delete">
                <DeleteIcon />
              </IconButton>
            </Tooltip>
          </Box>
        </Grid>
      </ListItem>
      <Divider />
    </>
  );
};

export default IntentRegNluItem;
