import React from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
// import List from "@material-ui/core/List";
import PropTypes from "prop-types";
import IntentRegNluItem from "./IntentRegNluItem";
import { FixedSizeList } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";
import InfiniteLoader from "react-window-infinite-loader";
import { getTrainingData } from "./actions/nlu.actions";

let items = {};
let requestCache = {};
let g_intents = [];
const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    height: 700,
    maxWidth: 1500,
    backgroundColor: theme.palette.background.paper
  }
}));

const renderRow = props => {
  const { index, style, data, total, limit, offset, intents } = props;
  const item = items[index];
  console.log("renderRow props", props);
  console.log("renderRow", index);
  console.log("renderRow item", item);

  return (
    <IntentRegNluItem
      index={index}
      style={style}
      data={item}
      total={total}
      limit={limit}
      offset={offset}
      intents={g_intents}
    />
  );
};
renderRow.propTypes = {
  index: PropTypes.number.isRequired,
  style: PropTypes.object.isRequired
};

const isItemLoaded = ({ index }) => !!items[index];

const loadMoreItems = (visibleStartIndex, visibleStopIndex) => {
  console.log(visibleStartIndex, visibleStopIndex);
  const key = [visibleStartIndex, visibleStopIndex].join(":"); // 0:10
  if (requestCache[key]) {
    return;
  }

  const length = visibleStopIndex - visibleStartIndex;
  const visibleRange = [...Array(length).keys()].map(
    x => x + visibleStartIndex
  );
  const itemsRetrieved = visibleRange.every(index => !!items[index]);

  if (itemsRetrieved) {
    requestCache[key] = key;
    return;
  }

  return getTrainingData("default", {
    q: "",
    intent: "",
    limit: length,
    offset: visibleStartIndex
  })
    .then(response => response.data)
    .then(data => {
      // data.records.forEach((city, index) => {
      //   items[index + visibleStartIndex] = city.fields;
      // });
      data.forEach((item, index) => {
        items[index + visibleStartIndex] = item;
      });
      console.log(data);
    })
    .catch(error => console.error("Error:", error));
};

const IntentRegNluList = ({
  data,
  intents,
  name,
  index,
  total,
  limit,
  offset,
  onRetrieve
}) => {
  const classes = useStyles();
  console.log(data);
  console.log(intents);
  g_intents = intents;
  return (
    <AutoSizer>
      {({ height, width }) => (
        <InfiniteLoader
          isItemLoaded={isItemLoaded}
          loadMoreItems={loadMoreItems}
          itemCount={total}
        >
          {({ onItemsRendered, ref }) => (
            // <AutoSizer>
            //   {({ height, width }) => (
            <FixedSizeList
              className="List"
              height={height}
              itemCount={total}
              itemSize={80}
              width={width}
              // itemData={data}
              ref={ref}
              onItemsRendered={onItemsRendered}
            >
              {renderRow}
            </FixedSizeList>
            //   )}
            // </AutoSizer>
          )}
        </InfiniteLoader>
      )}
    </AutoSizer>
  );

  // return (
  //   <List>
  //     {data &&
  //       data.map((item, index) => {
  //         return (
  //           <IntentRegNluItem
  //             key={item.id}
  //             index={index}
  //             data={item}
  //             intents={intents}
  //           />
  //         );
  //       })}
  //   </List>
  // );
};

export default IntentRegNluList;
